import { NgModule } from '@angular/core';
import { UIRouterModule, RootModule, Ng2StateDeclaration } from 'ui-router-ng2';
import { TranslateService } from '@ngx-translate/core';

import { HomeComponent, InformationsComponent, DocumentsComponent, EventsComponent, ContactComponent } from './containers';

import { AppContainersModule } from './containers/app-containers.module';

// States

const routesConfig = [
  {
    hr: 'naslovnica',
    en: 'home',
    component: HomeComponent
  },
  {
    hr: 'informacije',
    en: 'informations',
    component: InformationsComponent
  },
  {
    hr: 'dokumenti',
    en: 'documents',
    component: DocumentsComponent
  },
  {
    hr: 'dogadaji',
    en: 'events',
    component: EventsComponent
  },
  {
    hr: 'kontakt',
    en: 'contact',
    component: ContactComponent
  },
];

const routesStates = [];

// build default root states with complmeent language pairs
routesConfig.forEach(routeConfig => {
  ['hr', 'en'].forEach((language: string) => {
    routesStates.push({
      name: routeConfig[language],
      url: '/' + routeConfig[language],
      component: routeConfig.component,
      // setup for easy language change
      config: routeConfig,
      resolve: [
        {
          token: 'language',
          deps: [TranslateService],
          resolveFn: (translate) => {
            translate.use(language);
          }
        }
      ]
    });
  });
});


// END - States

const routes = {
  states: routesStates,
  useHash: false,
  otherwise: {
    state: routesStates[0].name
  }
};

@NgModule({
  imports: [
    AppContainersModule,
    UIRouterModule.forRoot(routes)
  ],
  exports: [UIRouterModule]
})
export class AppRoutingModule { }



